.brandName{
    font-family: ProximaNova-Semibold;
    font-size: 0.8rem;
    color: #666;
   
    width: 36px;
}
.title{
    font-size: 1.5rem;
    color: #222;
    padding-top: 0.6rem;
    font-weight: 400;
    margin: 0 0 10px;
   
}
.mrpStyle{
    font-size: 0.8rem;
    color: #8f8f8f;
}
.spStyle{
    color: #222;
    font-size: 1.1rem;
    font-weight: 500;
}
.dpStyle{
    color: #ba5252;
    font-size: 0.8rem;
    font-weight: bold;
}
.info{
    font-size: 0.8rem;
    color: #8f8f8f;
}

.basketBtn{
    cursor: pointer;
    margin: 0 5px;
    background: #84c225;
    color: #fff;
    border: 1px solid #84c225;
    border-radius: 3px;
    padding: 11px 30px;

}
.saveBtn{
    color: #666;
    border: 1px solid #979797;
    border-radius: 3px;
    position:relative;
    margin: 0 5px;
    padding: 11px 30px;
    cursor: pointer;
}
.deliveryTime{
    color: #979797;
    font-size : 0.8rem;
    padding: 10px
}
.optionBox{
    cursor: pointer;
    font-size: .8rem;
    min-height: 54px;
    border-radius: 0.3rem;
    overflow: hidden;
    min-height: 3rem;
    border: 1px solid #d0d0d0;
    display: flex;
    margin : 8px;
    
}
.optionBox:hover{
    box-shadow: 0 2px 2px 0 rgba(0,0,0,.2);
}
.weight{
    width: 27%;
    margin: 1rem 0 1rem 1rem;
    flex-grow: 0;
    padding-right: 1%;
}
.rate{
    flex-grow: 1;
    margin: 1rem 0;
}
.rate span{
    margin-left: 1rem
}
.selectOption{
    width: 3.6rem;
    flex-shrink: 0;
    align-self: stretch;
    background: 
    url(data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='16' height='12'%3E%3Cpath fill='%238F8F8F' fill-rule='evenodd' d='M15.904.185l-.092-.091a.322.322 0 00-.457.002L4.647 10.893a.162.162 0 01-.232-.003L.653 6.893a.323.323 0 00-.457-.014l-.095.089a.325.325 0 00-.014.457L4.27 11.87a.323.323 0 00.465.006L15.906.643a.325.325 0 00-.002-.458'/%3E%3C/svg%3E)
     no-repeat 50%
}
.saveBtn{
    box-shadow: 0 2px 2px 0 rgba(0,0,0,.2);
    position: relative;
    background: #fff;
    margin: 0 5px;
    padding: 11px 30px
}
.clickedWeight{
    background: rgba(132,194,37,.2);
    border : none;
}
.clickedWeight .selectOption{
    background: url(data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='16' height='13'%3E%3Cpath fill='%23FFF' fill-rule='evenodd' d='M15.904.785l-.092-.091a.322.322 0 00-.456.002L4.646 11.493a.162.162 0 01-.231-.003L.653 7.493a.323.323 0 00-.457-.014l-.094.089a.325.325 0 00-.014.457L4.27 12.47a.323.323 0 00.465.006L15.906 1.243a.325.325 0 00-.002-.458'/%3E%3C/svg%3E) no-repeat 50%;
    background-color: #9cc63d;
    border-radius: 0 3px 3px 0;
    margin: -1px -1px -1px 0
}
.headingStyle{
    font-size: 1.8rem;
    padding: 1rem;
    border-bottom: 1px solid #eee;
    margin: 0;
    font-weight: 400;
    color: #222;
}
.subHeading{
    font-size: 1.0rem;
    cursor: pointer;
    color: #444;
}
.content{
    color: #666;
    line-height: 1.4;
    font-size: 0.9rem;
}